import equal from 'fast-deep-equal';
import { Controller, Document } from '@ali/4ever-cangjie';
import { canUndo } from '../queries/canUndo';

export function undoInput(controller: Controller) {
  let previousDocument: Document | null = null;
  do {
    previousDocument = controller.value.document;
    if (controller.query(canUndo)) {
      controller.command('undo');
    } else {
      break;
    }
  // TODO: 使用自写的 is 替换 fast-deep-equal
  } while (equal(controller.value.document, previousDocument));
  return controller;
}
